<!--
	* PokemonPicture.vue
	* este componente se va a encargar de mostrar la imagen
	* debe saber cuando hacerlo 
-->

<template>
  <div class="pokemon-container">
    <!--
		* no olvidar hacer el v-bind o :
		* a la propiedad computada en este caso al imgSrc
	-->
    <img
      :src="imgSrc"
      class="hidden-pokemon"
      alt="pokemon"
    />

    <!--
		* Si showPokemon está en true -> muestro la imagen
	-->
    <img
      v-if="showPokemon"
      :src="imgSrc"
      class="fade-in"
      alt="pokemon"
    />
  </div>
</template>

<script>
  export default {
    props: {
      pokemonId: {
        type: Number,
        required: true,
      },

      showPokemon: {
        type: Boolean,
        required: true,
        default: false,
      },
    },

    computed: {
      imgSrc() {
        return `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${this.pokemonId}.svg`
      },
    },
  }
</script>

<!--
	* Con la palabra scope decimos que este style se aplique solo a este componente
	* de otra manera se aplica de manera global
-->
<style scoped>
  /* Pokemon Picture */
  .pokemon-container {
    height: 200px;
  }

  img {
    height: 200px;
    position: absolute;
    width: 100vw;
    margin-left: -50vw;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
  }

  .hidden-pokemon {
    filter: brightness(0); /*con esto vamos a ver solo la silueta de la imagen*/
  }
</style>
