import pokemonApi from '@/api/pokemonApi'

const getPokemons = () => {
  // esto crea una arreglo con 650 posiciones vacias
  const pokemonsArr = Array.from(Array(650))

  //console.log(pokemonsArr)

  // => barrer cada uno de los elementos y poner el valor del 1 al 651
  return pokemonsArr.map((_, index) => index + 1)
}

//* RECORDAR QUE ESTO ES UNA PROMESA -> por el async
const getPokemonOptions = async () => {
  //* para desordenar el array de 650 items de manera randómica
  const mixedPokemons = getPokemons().sort(() => Math.random() - 0.5)

  //console.log(getPokemons())

  //console.log(mixedPokemons)

  // => mandar los primeros 4 del mixedPokemons
  const pokemons = await getPokemonNames(mixedPokemons.splice(0, 4))
  //console.log(pokemons)
  //console.table(pokemons)

  return pokemons
}

//* Función para saber los nombres de estos pokemons
// con desestructuración vamos a sacar a cada pokemon
const getPokemonNames = async ([a, b, c, d] = []) => {
  //const resp = await pokemonApi.get(`/1`)
  //console.log(resp.data.name, resp.data.id) // Con `/1`: bulbasur 1

  //console.log(a, b, c, d)

  //? disparar promesas de manera simultánea

  const promiseArr = [
    pokemonApi.get(`${a}`),
    pokemonApi.get(`${b}`),
    pokemonApi.get(`${c}`),
    pokemonApi.get(`${d}`),
  ]

  //const resps = await Promise.all(promiseArr)
  //console.log(resps)

  //? Usando desestructuracion
  const [p1, p2, p3, p4] = await Promise.all(promiseArr)

  return [
    { name: p1.data.name, id: p1.data.id },
    { name: p2.data.name, id: p2.data.id },
    { name: p3.data.name, id: p3.data.id },
    { name: p4.data.name, id: p4.data.id },
  ]
}

export default getPokemonOptions
