<template>
  <!--
	* v-if & v-else
	* intenta verificar si pokemon tiene un valor -> si no lo tiene muestra el mensaje de esperar
	* caso contrario me muestra la imagen del pokemon e inicia el juego
  -->
  <h1 v-if="!pokemon">Espere por favor...</h1>

  <div v-else>
    <h1>¿Quién es este Pokemon?</h1>
    <h5>Curso Vue.js from @FernandoHerrera - @SebasSilvaP</h5>

    <!-- //? Opción 1 para escribir el componente -->
    <!--  
    <PokemonPicture
      :pokemonId="pokemon.id"
      :showPokemon="showPokemon"
    />
	-->

    <!--
		* NOTAR POR FAVOR LA SEMÁNTICA QUE SE UTILIZA EN VUE
		* selectionPokemon  ==> en PokemonOptions.vue
		* selection-Pokemon ==> en PokemonPage.vue (también podemos ponerlo como selectionPokemon)
	-->

    <!--
		* Esta misma semántica también la podemos aplicar para los componentes
		* Por ejemplo para el PokemonPicture
	-->

    <!-- //? Opción 2 para escribir el componente -->
    <!--  
	<pokemon-picture
      :pokemon-id="pokemon.id"
      :show-pokemon="showPokemon"
    ></pokemon-picture>
	-->

    <!-- //? Opción 3- Sugerida por el instructor (combinando las 2 anteriores) -->
    <PokemonPicture
      :pokemon-id="pokemon.id"
      :show-pokemon="showPokemon"
    />

    <PokemonOptions
      :pokemons="pokemonArr"
      @selection-pokemon="checkAnswer"
    />

    <!--
		* con el template en vez de div
		* este elemento no aparece en el html de la página
	-->
    <div v-if="showAnswer">
      <h2 id="answerMessage">{{ message }}</h2>
      <button
        @click="newGame"
        class="resetBtn"
      >
        Nuevo Juego
      </button>
    </div>

    <!--
	* para mandarlo a llamar
	* vamos a utilizar los hooks de los ciclos de vida en Vue
	* métodos que son llamados en cierto momento en cualquier cosa que pasa en el componente
	* algo que se engancha en la forma como se construye y se destruye el componente

	* RECORDAR: cuando veamos Handler y Target -> es un objeto REACTIVO !!
  	-->
  </div>
</template>

<script>
  // manera tradicional de hacer importaciones (tambien se puede hacer con Lazy Load)
  // RECORDAR: @/ => se viene al src y desde ahi buscamos

  import PokemonOptions from '@/components/PokemonOptions.vue'
  import PokemonPicture from '@/components/PokemonPicture.vue'

  import getPokemonOptions from '@/helpers/getPokemonOptions'

  //console.log(getPokemonOptions()) // esto es una promesa, lo dejamos así y sale promesa pendiente

  export default {
    components: { PokemonOptions, PokemonPicture },

    // RECORDAR
    // - data es un método que debe retornar un objeto
    // - este objeto va a ser reactivo
    // - es decir, Vue va a estar pendiente de cualquier cambio que suceda
    // - Cuando cambie algo, va a renderizar lo que haya que renderizar

    data() {
      return {
        pokemonArr: [],
        pokemon: null,
        showPokemon: false,

        // para mostrar cuando esta correcto o no
        showAnswer: false,
        message: '',
      }
    },

    methods: {
      // recordar que getPokemonOptions es una promesa
      // los métodos también pueden ser async
      async mixPokemonArray() {
        this.pokemonArr = await getPokemonOptions()

        const rndInt = Math.floor(Math.random() * 4)
        this.pokemon = this.pokemonArr[rndInt]

        //console.log(this.pokemonArr)
      },

      checkAnswer(selectedId) {
        //console.log('Pokemon Page llamado', pokemonId)
        // => cuando la persona hace click, tengo que mandar la imagen del Pokemon

        this.showPokemon = true
        this.showAnswer = true

        if (selectedId === this.pokemon.id) {
          this.message = `Correcto ❤️, ${this.pokemon.name}`
        } else {
          this.message = `Oops 😢, el pokemon era ${this.pokemon.name}`
        }
      },

      // método para reiniciar el juego
      newGame() {
        this.showPokemon = false
        this.showAnswer = false
        this.pokemonArr = []
        this.pokemon = null
        this.mixPokemonArray()
      },
    },

    // mounted -> de una sola vez
    mounted() {
      //console.log('mounted')
      this.mixPokemonArray()
    },
  }
</script>

<style scoped>
  .resetBtn {
    background: linear-gradient(to left top, #1d1c1c, #8a92ee);
    border-color: rgb(255, 230, 10);
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
      'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 15px;
    padding: 10px;
    text-transform: uppercase;
  }

  h5 {
    color: blue;
  }
</style>
