    <!-- 
		* Utilizando un v-for 
		* es una buena práctica colocar el key -> de esta manera Vue lo identifica de manera única
		* el evento click va a emitir el id del pokemon
		* símbolo de dólar es un caracter común y corriente - con el símbolo de dólar y emit -> $emit, si tenemos un método que se llame emit, este y lo que ponemos aquí son diferentes
	-->
    <ul>
      <li
        v-for="pokemon in pokemons"
        :key="pokemon.id"
        @click="$emit('selectionPokemon', pokemon.id)"
      >
        {{ pokemon.name }}
      </li>
    </ul>
  </div>
</template>

